 .fotos{
    justify-content: center;
    padding-left: 20px;
    padding-top: 7px;
    margin-bottom: 25px;
    padding-bottom: 7px;
    padding-right: 20px;
    background-color: rgba(85, 85, 84, 0.986);
    border-bottom: solid 1.5px rgba(255, 255, 255, 0.39);
}

.fotos img{
    border-radius: 100%;
    border: solid rgba(255, 255, 255, 0.384) 2px;
    margin-right: 10px;
}

.fotos img:hover{
    border: solid rgba(154, 205, 50, 0.384) 2px;
}

.nombres{
    display: flex;
    flex-direction: column;
    line-height: 0;
    color:whitesmoke;
    margin: 0;
    justify-content: center;
    
    padding-bottom: 10px;
}

.nombres h2{
    font-style: italic;
}

.nombres h3{
    font-size: 15px;
    margin: 0;
    color: yellowgreen;
}

 @media only screen and (max-width: 1122px) {
     .fotos{
    justify-content:center;
    padding-left: 0px;
    padding-top: 5px;
    margin-bottom: 0px;
    padding-bottom: 5px;
    padding-right:0px;
    background: none;
    border-bottom: none;
    height: fit-content;
}

.fotos img{
    border-radius: 100%;
    border: solid rgba(255, 255, 255, 0.384) 2px;
    margin-right: 10px;
    height: fit-content;
    display: block;
    
}

.fotos img:hover{
    border: solid rgba(154, 205, 50, 0.384) 2px;
}

.nombres{
    display: none;
    padding-bottom: 0px;
}

.fotos img{
    width: 40px;
    height: 40px;
}

} 