
.columns{
    display: flex;
    flex-direction: inline;
    padding-top: 40px;
    color: white;
    margin-top: 25px;
    text-align: center;
    justify-content: center;
}


.firstColumn{
    padding-right: 100px;
}

.firstColumn p{
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    font-size: 20px;
    font-style: italic;
}

.secondColumn img{
    margin-bottom: 20px;
    box-shadow: 0px 0px 10px 0px #969393;
    border-radius: 100%;
    
}

.firstColumn h1{
    font-size: 80px;
    text-align: center;
    line-height: 0;
    padding-top: 15px;
    white-space: nowrap;
}


.proyectos{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    text-align: center;
    overflow: hidden;
}



.titulo h3{
    text-align: center;
    color: white;
    font-size: 30px;
    padding-top: 20px;
}


.imgbutton{
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

button{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: rgba(44, 43, 43, 0.521);
    color: white;
    border: rgba(255, 255, 255, 0.144) 1px solid;
    font-size: 18px;
}

button:hover{
    background-color: black;
    color: white;
    cursor: pointer;
}


.boton{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: rgba(44, 43, 43, 0.521);
    color: white;
    border: rgba(255, 255, 255, 0.144) 1px solid;
    font-size: 17px;
    text-decoration: none;
    margin-top: 10px;
    padding: 4px;
    text-align: center;
    
}

.boton:hover{
    background-color: black;
    color: white;
    cursor: pointer;
}

.hache{
   border: rgba(88, 85, 85, 0.521) 1px solid;
}

.boton2{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: rgba(44, 43, 43, 0.521);
    color: white;
    border: rgba(255, 255, 255, 0.144) 1px solid;
    font-size: 17px;
    text-decoration: none;
    margin-top: 10px;
    padding: 4px;
}

.boton2:hover{
    background-color: black;
    color: white;
    cursor: pointer;
}


.proyectos img{
    border: solid 2px rgba(255, 255, 255, 0.212);
}

.proyectos img:hover{
    border: solid rgba(154, 205, 50, 0.212) 2px;
}

strong {
    color: yellowgreen;
}

.skills{
   color: rgba(207, 199, 199, 0.699);
   font-style: normal;
}

@media screen and (max-width: 1400px) {
    .proyectos img{
        width: 310px;
        height: 190px;
    }  
}

@media screen and (max-width: 1260px) {
    .proyectos img{
        width: 350px;
        height: 210px;
    }

    .proyectos{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        text-align: center;
        overflow: hidden;
    }
    
    .proyectos div{
        margin-top: 10px;
    }
   
    .columns{
        padding-top: 0px;
    }
    .secondColumn{
        display: none;   
     }

     .firstColumn{
         padding-right: 0;
         text-align: center;
         justify-content: center;
     }

     .hache{
        margin-top: 50px;
    }
    
}
@media screen and (max-width: 900px) {
    .proyectos{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        text-align: center;
        overflow: hidden;
    }
    
    .proyectos div{
        margin-top: 10px;
    }
    
    .secondColumn{
       display: none;   
    }

    .columns{
        width: 100%;
        padding-top: 0px;
    }

    .firstColumn{
        padding-right: 0px;
        margin-right: 0px;
    }


    .firstColumn h1 {
        font-size: 70px;
        width: 100%;
        padding-top: 5px;
    }

    .firstColumn p {
        font-size: 20px;
    }

    .hache{
        margin-top: 50px;
    }

}